/* Home.css */
.home {
    background: linear-gradient(#C6CF9B, #596FB7, #11235A);
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
    height: 100vh;
    font-size: large;
    position: relative;
    overflow: hidden;
  }
  
  p,
  h1,
  h2 {
    font-family: Georgia, "Times New Roman", Times, serif;
    color: #fff;
    text-align: center;
    line-height: 1.5;
  }
  
  small {
    font-size: 17px;
    font-family: 'Times New Roman', Times, serif;
    font-style: italic;
  }
  
  .social-icons {
    align-items: left;
  }
  
  .round_image {
    width: 200px;
    height: 200px;
    border-radius: 50%;
    object-fit: cover;
    position: relative;
    z-index: 1;
  }
  
  .round-image-container {
    position: relative;
    width: 220px;
    height: 220px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .round-image-border {
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    border: 5px solid transparent;
    border-top: 5px solid #0d057a;
    animation: rotateBorder 2s linear infinite;
    z-index: 0;
  }
  
  @keyframes rotateBorder {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
  
  .home-content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
  }
  
  .home-text {
    flex: 1;
    padding-right: 20px;
  }
  
  .button-container {
    display: flex;
    gap: 20px;
    justify-content: center;
    margin-top: 20px;
  }
  
  .more-button,
  .resume-button {
    background-color: #6494dd;
    align-items: center;
    color: white;
    font-size: 20px;
    padding: 10px 24px;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    text-decoration: none;
    transition: background-color 0.3s;
  }
  
  .more-button:hover,
  .resume-button:hover {
    background-color: #647265;
  }
  
  .icon-container {
    display: flex;
    justify-content: center;
    gap: 20px;
    margin-top: 20px;
  }
  
  .icon-link {
    color: #fff;
    font-size: 2em;
    transition: color 0.3s;
  }
  
  .icon-link:hover {
    color: #007bff;
  }
  